@import url(https://fonts.googleapis.com/css2?family=Rajdhani&display=swap);
html,body{margin:0;height:100%;width:100%;padding:0}html{scroll-behavior:smooth;background:#fdf21d;background:linear-gradient(144deg, #fddb6b 0%, #fd4d4d 100%);background-repeat:no-repeat;background-size:cover}body{background:#fdf21d;background:linear-gradient(144deg, #fddb6b 0%, #fd4d4d 100%);background-repeat:no-repeat;background-size:cover;margin:0;font-family:"Rajdhani", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}

.column{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;width:18em;height:30em;position:relative;top:10vh;border-radius:15px;box-shadow:0 0 1rem 0 #222831;border-radius:15px;background-color:rgba(255,255,255,0.685);-webkit-backdrop-filter:blur(30px);backdrop-filter:blur(30px)}@media (max-width: 768px){.column{left:0;top:0}}.column .top{display:flex;flex-direction:column;justify-content:center;align-items:center}.column .top img{width:150px;border-radius:100px}.column .top h1{font-size:30px;margin-bottom:0}.column .top h1 strong{font-weight:bold}.column .top h2{background-color:#2d3238;color:#F8F7FF;border-radius:100px;font-size:15px;padding:5px 10px;margin-top:0}.column .top a{margin-left:10px}.column .top a .logo{width:20px;border-radius:0}.column .logo{width:20px;border-radius:0}.column .info{display:flex;flex-direction:column;align-items:flex-start}.column .info .row{display:flex;justify-content:center;align-items:center;margin:0}.column .info .row p{margin:0;margin-left:5px}.column button{background-color:transparent;border-radius:5px solid black;border-color:#2d3238;border-radius:100px;padding:5px 10px;font-weight:bold}.column button:hover{background-color:#2d3238;border-color:transparent}.column button:hover a{color:#F8F7FF}.column button a{text-decoration:none;color:#2d3238}

.column-menu{display:flex;flex-direction:column;justify-content:space-between;width:65vw;height:28em;position:relative;top:10vh;box-shadow:0 0 1rem 0 #222831;border-radius:15px;background-color:rgba(255,255,255,0.685);-webkit-backdrop-filter:blur(30px);backdrop-filter:blur(30px);padding:1em}@media (max-width: 768px){.column-menu{height:auto;width:16em;top:1em}}.column-menu .aboutme .row{display:flex;justify-content:space-between;align-items:center;margin-bottom:10px}.column-menu .aboutme p{margin-top:0}.column-menu .aboutme h1{margin:0;margin-left:20px}@media (max-width: 1024px){.column-menu .aboutme h1{margin:0;margin-left:20px}}.column-menu .aboutme h1 strong{border-bottom:3px solid black;border-color:#222831}.column-menu .doing{display:flex;flex-direction:row;flex-wrap:wrap}.column-menu .doing h1{margin-top:0;margin-left:20px}@media (max-width: 1024px){.column-menu .doing h1{margin:0;margin-left:20px}}.column-menu .doing .cards{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-around}@media (max-width: 768px){.column-menu .doing .cards{flex-direction:column}}.column-menu .doing .cards .card{box-shadow:0 0 0.1rem 0 #222831;border-radius:15px;background-color:rgba(252,215,181,0.534);-webkit-backdrop-filter:blur(30px);backdrop-filter:blur(30px);display:flex;margin:5px;width:45%;padding:0}@media (max-width: 768px){.column-menu .doing .cards .card{width:auto}}.column-menu .doing .cards .card .icon{padding:3px;width:50px}.column-menu .doing .cards .card .icon-mid{padding:5px;padding-top:10px;width:35px}.column-menu .doing .cards .card .icon-min{padding:5px;padding-top:10px;width:25px}.column-menu .doing .cards .card .right h3{font-size:15px}@media (max-width: 1024px){.column-menu .doing .cards .card .right p{margin:0}}.column-menu .listofprojects{display:flex;flex-direction:row;flex-wrap:wrap;justify-content:space-between}.column-menu .listofprojects .project{flex:0 0 25%;margin:5px}.column-menu .listofprojects .project .pj-img{width:250px;height:150px;object-fit:cover;border-radius:15px}.column-menu .listofprojects .project .click{box-shadow:0 0 1rem 0 rgba(0,0,0,0.2);border-radius:5px;position:fixed;z-index:99;background:inherit;overflow:hidden;top:2em;left:10em;width:38em;border-radius:15px;padding:1em;display:none;flex-direction:column;justify-content:center;align-items:center;box-shadow:0 0 1rem 0 rgba(0,0,0,0.2);background-color:rgba(255,255,255,0.466);-webkit-backdrop-filter:blur(30px);backdrop-filter:blur(30px)}@media (max-width: 1024px){.column-menu .listofprojects .project .click{left:5em;width:38em}}@media (max-width: 768px){.column-menu .listofprojects .project .click{top:0em;left:0em;width:auto}}.column-menu .listofprojects .project .click .close{position:relative;top:-5em;right:-13.5em;font-size:22px}.column-menu .listofprojects .project .click .row{display:flex;justify-content:space-evenly;align-items:center;flex-wrap:wrap}.column-menu .listofprojects .project .click .row p{margin-left:5px;text-transform:uppercase;font-weight:bold;background-color:#2d3238;color:#F8F7FF;border-radius:100px;font-size:13px;padding:3px 10px;margin-top:0}.column-menu .listofprojects .project .click .links{display:flex;flex-wrap:wrap;justify-content:center}.column-menu .listofprojects .project .click .links a{margin:0 5px;background-color:transparent;border:1px solid black;border-color:#2d3238;border-radius:100px;padding:5px 10px;font-weight:bold;text-decoration:none;color:#2d3238}.column-menu .listofprojects .project .click .links a:hover{background-color:#2d3238;border-color:transparent;color:#F8F7FF}.column-menu .listofprojects .project .click .links a a{text-decoration:none}

.navbar{margin:0;padding:0;display:flex;flex-direction:row;align-items:center;justify-content:center}.navbar img{width:25px}.navbar .menu button{padding:0.5em 1em;background-color:transparent;border:2px solid black}.navbar .menu button:hover{background-color:#2d3238;border-color:transparent;color:#F8F7FF}.navbar .menu button:first-child{border-radius:5px 0 0 5px;border-right:none}

.container{display:flex;justify-content:space-evenly;background:#fdf21d;background:linear-gradient(144deg, #fddb6b 0%, #fd4d4d 100%) no-repeat center center fixed;background-size:cover}@media (max-width: 768px){.container{flex-direction:column;justify-content:center;align-items:center;padding:1em}}

